/* Typography & Titles */
#title-Tag-Line {
  font-size: 20px;
}

.pageTitle {
  font-size: 2em;
  font-weight: bold;
}

.secondaryTitle {
  color: grey;
}

.title {
  display: block;
  font-family: sans-serif;
  margin: 10px auto 5px;
  width: 300px;
}

.termsConditions {
  margin: 0 auto 5px 80px;
}

/* Contact Form Container */
.contactForm {
  background: #fff;
  box-shadow: 0 30px 60px rgba(90, 116, 148, 0.4);
  border-radius: 8px;
  max-width: 540px;
  margin: 40px auto;
  padding: 30px;
  border-top: 5px solid #3abdc1;
  animation: bounce 1.5s infinite;
}

/* Input + Textarea Styling */
.formEntry {
  display: block;
  width: 100%;
  margin: 20px 0;
  padding: 12px 16px;
  border-radius: 4px;
  border: none;
  background-color: #ebebeb;
  font-size: 1em;
  transition: all 0.3s ease-in-out;
}

/* Hover Effects */
.formEntry:hover {
  border-bottom: 4px solid #3abdc1;
  background-color: #e4f4f5;
}

/* Placeholder Styling */
::-webkit-input-placeholder {
  font-size: 1.1em;
}

/* Message Field Specific */
textarea.formEntry {
  height: 8rem;
  resize: vertical;
}

/* Submit Button */
.submit {
  display: block;
  width: 200px;
  margin: 30px auto 0;
  color: white;
  background-color: #3abdc1;
  font-size: 18px;
  padding: 12px 0;
  border: none;
  border-radius: 4px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.submit:hover {
  background-color: #4383ba;
  transform: translateY(-3px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
}

/* Error Messages */
.errorMessage {
  color: red;
  font-size: 0.9em;
  margin-top: -15px;
  margin-bottom: 10px;
  display: block;
}

/* Bounce Animation */
@keyframes bounce {
  0% {
    transform: translate(0, 4px);
  }
  50% {
    transform: translate(0, 8px);
  }
  100% {
    transform: translate(0, 4px);
  }
}
