/* Tailwind Directives */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Base Typography */
body {
  background-color: #f3f4f6; /* bg-gray-100 */
  color: #111827; /* text-gray-900 */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Inter', system-ui, sans-serif;
  line-height: 1.75;
}

/* Article Container */
article {
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 10px 15px -3px rgba(0,0,0,0.1), 0 4px 6px -4px rgba(0,0,0,0.1);
  margin-bottom: 4rem;
  max-width: none;
}

/* Headings & Typography */
article h1 {
  font-size: 2.25rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  color: #111827;
  line-height: 1.25;
}
article h2 {
  font-size: 1.875rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: #111827;
  line-height: 1.375;
}
article h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: #1f2937;
}
article p {
  font-size: 1.125rem;
  color: #374151;
  margin-bottom: 1rem;
}
article ol,
article ul {
  margin-left: 1.5rem;
  margin-bottom: 1.25rem;
  font-size: 1rem;
  color: #374151;
}
article ol {
  list-style-type: decimal;
}
article ul {
  list-style-type: disc;
}
article blockquote {
  border-left: 4px solid #60a5fa;
  padding-left: 1.5rem;
  font-style: italic;
  color: #4b5563;
  margin: 1.5rem 0;
}

/* Anchor Styling */
article a {
  color: #2563eb;
  font-weight: 500;
  text-decoration: underline;
  text-underline-offset: 2px;
  transition: color 0.2s;
}
article a:hover {
  color: #1e40af;
}

/* Responsive Video Embeds */
.aspect-w-16 {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%;
  margin-bottom: 1rem;
}


.aspect-w-16 iframe {
  position: absolute;
  top: 0;
  left: 0;
  
  width: 100%; /* Fill the container width */
  height: 100%; /* Fill the container height */
}




/* Responsive container for the video */
.video-container {
  position: relative;
  width: auto; /* Take full width of its parent */
  padding-bottom: 177.78%; /* For a 9:16 aspect ratio video (9 / 16 * 100% = 56.25% for 16:9, so 16 / 9 * 100% = 177.78% for 9:16) */
  height: 0; /* Important to collapse height */
  overflow: hidden; /* Hide anything that spills out */
}

/* Styles for the iframe itself */
.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  
  width: 100%; /* Fill the container width */
  height: 100%; /* Fill the container height */
}

/* Media query for mobile devices (adjust breakpoint as needed) */
@media (max-width: 768px) { /* Example breakpoint for mobile */
  .aspect-w-9 {
    /* For mobile, you might want to adjust the aspect ratio if the 9:16 video
       is too tall and causes issues with other content.
       However, if you strictly want to show the full 9:16 video without scroll,
       the 177.78% padding-bottom will achieve that.
       If you want the video to fit within the screen height without dominating,
       you might consider a different approach like letterboxing or cropping,
       or simply accepting that the video will take up significant vertical space.
    */
    /* If you wanted to, for example, force a 16:9 ratio on mobile to reduce height: */
    /* padding-bottom: 56.25%; */
    /* This would crop the top/bottom of your 9:16 video to fit 16:9,
       but it would prevent vertical scrolling due to the video's height. */
  }
}

@media (max-width: 768px) { /* Example breakpoint for mobile */
  .video-container {
    /* For mobile, you might want to adjust the aspect ratio if the 9:16 video
       is too tall and causes issues with other content.
       However, if you strictly want to show the full 9:16 video without scroll,
       the 177.78% padding-bottom will achieve that.
       If you want the video to fit within the screen height without dominating,
       you might consider a different approach like letterboxing or cropping,
       or simply accepting that the video will take up significant vertical space.
    */
    /* If you wanted to, for example, force a 16:9 ratio on mobile to reduce height: */
    /* padding-bottom: 56.25%; */
    /* This would crop the top/bottom of your 9:16 video to fit 16:9,
       but it would prevent vertical scrolling due to the video's height. */
  }
}

/* Sidebar */
aside {
  position: sticky;
  top: 6rem;
  background-color: #ffffff;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: 0 10px 15px -3px rgba(0,0,0,0.1), 0 4px 6px -4px rgba(0,0,0,0.1);
}
aside h4 {
  font-weight: 600;
  font-size: 1.125rem;
  margin-bottom: 0.75rem;
  color: #1f2937;
}
aside ul {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
aside ul li a {
  display: block;
  color: #2563eb;
  font-weight: 500;
  transition: color 0.2s;
}
aside ul li a:hover {
  color: #1e40af;
}

/* Featured/Banner Section */
.featured {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
  align-items: center;
  background-color: #eff6ff;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 6px rgba(0,0,0,0.05);
  margin-bottom: 4rem;
}
@media (min-width: 1024px) {
  .featured {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
.featured h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: #1f2937;
}
.featured strong {
  color: #2563eb;
  font-size: 0.875rem;
  font-weight: 600;
}
.featured p {
  font-size: 1rem;
  color: #374151;
}
.featured a {
  display: inline-block;
  margin-top: 1rem;
  background-color: #2563eb;
  color: #ffffff;
  padding: 0.5rem 1.25rem;
  border-radius: 0.375rem;
  transition: background-color 0.2s;
}
.featured a:hover {
  background-color: #1e40af;
}

/* Optional: Table of Contents */
.toc {
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
.toc a {
  display: block;
  font-size: 0.875rem;
  color: #2563eb;
  padding-left: 0.5rem;
  margin-bottom: 0.25rem;
}
.toc a:hover {
  color: #1e40af;
}

/* Footer */
footer {
  margin-top: 5rem;
  padding: 2rem;
  background-color: #ffffff;
  text-align: center;
  color: #6b7280;
  font-size: 0.875rem;
  border-top: 1px solid #e5e7eb;
}

/* Custom: Tighten spacing between videos and following paragraph */
section > .aspect-w-16 + p {
  margin-top: 0 !important;
}

.bounce {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}