/* Basic reset/styling */
.consent body2 {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  padding: 20px;
}

 .container2 {
  max-width: 650px;
  margin: 40px auto;
  background: #fff;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.consent h1 {
  color: #333;
  font-size: 1.5em;
  text-align: center;
  margin-bottom: 25px;
}

/* Form-specific styles */
.consent .opt-in-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

.consent .opt-in-form input[type="tel"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 5px; /* Reduced margin to make space for error message */
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
}

.consent .consent-text {
  display: flex;
  align-items: flex-start;
  margin-top: 15px;
  margin-bottom: 25px;
}

.consent .consent-text input[type="checkbox"] {
  margin-right: 10px;
  margin-top: 3px;
  flex-shrink: 0;
}

.consent .consent-text label {
  font-size: 0.9em;
  font-weight: normal;
  line-height: 1.4;
  display: inline;
}

.consent .disclaimer {
  font-size: 0.75em;
  color: #888;
  text-align: center;
  margin-top: 20px;
}

.consent button {
  background-color: #007bff;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
  font-size: 1em;
  transition: background-color 0.3s;
}

.consent button:hover:not(:disabled) {
  background-color: #0056b3;
}

.consent button:disabled {
  background-color: #a0c3e8;
  cursor: not-allowed;
}

/* Error and Success Messages */
.consent .error-message {
  color: #cc0000;
  font-size: 0.8em;
  margin-bottom: 10px;
}

.consent .success-box {
  margin-top: 20px;
  padding: 25px;
  background-color: #e6ffed;
  border: 1px solid #b3ffc7;
  color: #008000;
  border-radius: 4px;
  text-align: center;
}

.consent .success-box .disclaimer-note {
  font-size: 0.8em;
  color: #333;
  margin-top: 10px;
}

.consent .success-box button {
  margin-top: 20px;
  background-color: #28a745;
}